@charset "UTF-8";

/* _base.scss */
body,div,p,h1,h2,h3,h4,h5,h6,ul,ol,li,dl,dt,dd,table,th,td,form,fieldset,legend,input,textarea,button {margin:0;padding:0;}
body,input,textarea,select,table {font-family:Roboto, Arial, sans-serif;font-size:10px;}
body {-webkit-text-size-adjust: none;}
table {border-collapse:collapse;}
img,fieldset {border:0;}
img {vertical-align:top;}
li {list-style:none;}
em,address {font-style:normal;}
a {text-decoration:none;cursor: pointer;}
button {cursor:pointer;border:0;-webkit-border-radius:0;border-radius:0;background-color:transparent;}
.blind {overflow:hidden;position:absolute;clip:rect(0 0 0 0);width:1px;height:1px;margin:-1px;}

/* skip navigation */
#u_skip{position:relative;z-index:2147483647}
#u_skip a{position:absolute;left:-130px;top:-50px;width:128px;background:url(https://ssl.pstatic.net/static/common/sp_u_skip.png) repeat-x 0 -11px;border:1px solid #4ec53d;font-size:11px;letter-spacing:-1px;color:#fff;line-height:26px;text-align:center;outline:none}
#u_skip a:active, #u_skip a:focus{left:0;top:0;text-decoration:none;cursor:pointer;zoom:1}
#u_skip span{display:inline-block;padding:2px 6px 0 0;background:url(https://ssl.pstatic.net/static/common/sp_u_skip.png) no-repeat 100% 11px;}
